import React, { useEffect, useState } from "react"

import RBaseLayout from "../core/RBaseLayout"
import RSeo from "../core/RSeo"

import http from "./api/http"

import { Wrapper, Container } from "./styles"

export default function RBlog() {
  const [feedMira, setFeedMira] = useState(null)
  const [feedCrea, setFeedCrea] = useState(null)
  const [feedSenge, setFeedSenge] = useState(null)

  const [loading, setLoading] = useState(false)

  useEffect(() => {
    setLoading(true)

    http
      .get("https://www.amiranet.com.br/feeds.rss")
      .then(res => setFeedMira(res.rss.channel))
      .then(() => setLoading(false))
      .catch(e => console.error(e))
    http
      .get("https://portal.crea-sc.org.br/feed/")
      .then(res => setFeedCrea(res.rss.channel))
      .then(() => setLoading(false))
      .catch(e => console.error(e))
    http
      .get("http://www.senge-sc.org.br/category/em-destaque/feed/")
      .then(res => setFeedSenge(res.rss.channel))
      .then(() => setLoading(false))
      .catch(e => console.error(e))
  }, [])

  if (loading) {
    return (
      <RBaseLayout>
        <RSeo title="Blog" />
        <Wrapper>
          <h1 className="loading">Carregando notícias ...</h1>
        </Wrapper>
      </RBaseLayout>
    )
  }

  return (
    <RBaseLayout>
      <RSeo title="Blog" />
      <Wrapper>
        {feedMira && (
          <Container>
            <h2>Amiranet</h2>

            <div>
              {feedMira.item.map(i => {
                return (
                  <a
                    key={i.title._text}
                    href={i.link._text.replace(
                      "https://www.amiranet.com.br/novidade/",
                      "https://www.amiranet.com.br/noticia/"
                    )}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span>{i.title._text}</span>
                    <div
                      dangerouslySetInnerHTML={{ __html: i.description._text }}
                    />
                    <small>...</small>
                  </a>
                )
              })}
            </div>
          </Container>
        )}
        {feedCrea && (
          <Container>
            <h2>Crea SC</h2>

            <div>
              {feedCrea.item.map(i => {
                return (
                  <a
                    key={i.title._text}
                    href={i.link._text}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span>{i.title._text}</span>
                    <div
                      dangerouslySetInnerHTML={{
                        __html: i["content:encoded"]._cdata,
                      }}
                    />
                    <small>...</small>
                  </a>
                )
              })}
            </div>
          </Container>
        )}
        {feedSenge && (
          <Container>
            <h2>Senge SC</h2>

            <div>
              {feedSenge.item.map(i => {
                return (
                  <a
                    key={i.title._text}
                    href={i.link._text}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <span>{i.title._text}</span>
                    <div
                      dangerouslySetInnerHTML={{
                        __html: i["content:encoded"]._cdata,
                      }}
                    />
                    <small>...</small>
                  </a>
                )
              })}
            </div>
          </Container>
        )}
      </Wrapper>
    </RBaseLayout>
  )
}
